/**
 * Use this file for JavaScript code that you want to run in the front-end
 * on posts/pages that contain this block.
 *
 * When this file is defined as the value of the `viewScript` property
 * in `block.json` it will be enqueued on the front end of the site.
 *
 * Example:
 *
 * ```js
 * {
 *   "viewScript": "file:./view.js"
 * }
 * ```
 *
 * If you're not making any changes to this file because your project doesn't need any
 * JavaScript running in the front-end, then you should delete this file and remove
 * the `viewScript` property from `block.json`.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-metadata/#view-script
 */

document.addEventListener( 'DOMContentLoaded', function () {
	const modals = document.querySelectorAll( '.modal' );

	modals.forEach( function ( modal ) {
		const originalIframe = modal.querySelector( 'iframe' );
		const iframeWrapper = originalIframe?.parentNode;
		const originalIframeHTML = iframeWrapper?.innerHTML;

		modal.addEventListener( 'hide.bs.modal', function () {
			// ðŸ‘‡ Forza la rimozione del focus da qualunque elemento dentro la modale
			if (
				document.activeElement &&
				modal.contains( document.activeElement )
			) {
				document.activeElement.blur();
			}
		} );

		modal.addEventListener( 'hidden.bs.modal', function () {
			// Rimuove l'iframe completamente
			iframeWrapper.innerHTML = '';
		} );

		modal.addEventListener( 'show.bs.modal', function () {
			// Ripristina l'iframe al suo posto
			if ( originalIframeHTML ) {
				iframeWrapper.innerHTML = originalIframeHTML;
			}
		} );
	} );
} );
